'use client'

import React, { useState, useEffect } from 'react'
import { Input } from '@/components/ui/Input'
import { Button } from '@/components/ui/Button'

interface GroupFormProps {
  initialName?: string
  onSave: (name: string) => void
  onCancel: () => void
  existingNames?: string[]
}

export function GroupForm({ initialName = '', onSave, onCancel, existingNames = [] }: GroupFormProps) {
  const [name, setName] = useState(initialName)
  const [error, setError] = useState('')

  useEffect(() => {
    setName(initialName)
  }, [initialName])

  const validateName = (value: string): string => {
    const trimmed = value.trim()
    if (!trimmed) {
      return 'ËØ∑ËæìÂÖ•ÂàÜÁªÑÂêçÁß∞'
    }
    if (trimmed.length > 50) {
      return 'ÂêçÁß∞‰∏çËÉΩË∂ÖËøá50‰∏™Â≠óÁ¨¶'
    }
    if (existingNames.includes(trimmed) && trimmed !== initialName) {
      return 'ËØ•ÂêçÁß∞Â∑≤Â≠òÂú®'
    }
    return ''
  }

  const handleSubmit = () => {
    const validationError = validateName(name)
    if (validationError) {
      setError(validationError)
      return
    }

    onSave(name.trim())
  }

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSubmit()
    } else if (e.key === 'Escape') {
      onCancel()
    }
  }

  return (
    <div className="space-y-4">
      <div className="space-y-2">
        <label className="text-sm font-medium text-foreground">
          ÂàÜÁªÑÂêçÁß∞ <span className="text-destructive">*</span>
        </label>
        <Input
          value={name}
          onChange={(e) => {
            setName(e.target.value)
            setError(validateName(e.target.value))
          }}
          onKeyDown={handleKeyDown}
          error={error}
          placeholder="ËæìÂÖ•ÂàÜÁªÑÂêçÁß∞"
          autoFocus
        />
      </div>

      <div className="flex justify-end gap-2 pt-4">
        <Button
          type="button"
          variant="ghost"
          size="sm"
          onClick={onCancel}
        >
          ÂèñÊ∂à
        </Button>
        <Button
          type="button"
          size="sm"
          onClick={handleSubmit}
        >
          {initialName ? 'Êõ¥Êñ∞' : 'ÂàõÂª∫'}
        </Button>
      </div>
    </div>
  )
}